.chat-container {
  display: flex;
  flex-direction: column;
  flex: 0.7;

  .chat-header {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(215, 215, 215, 0.655);
    padding: 10px;
    p {
      margin: 0;
    }
    h3 {
      margin-block: 3px;
    }
    .chat-info {
      flex: 1;
      padding-left: 15px;
    }
  }

  .chat-body {
    padding: 15px;
    flex: 1;
    background: url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
    background-repeat: repeat;
    background-position: center;
    overflow: auto;
    .chat-message {
      position: relative;
      width: fit-content;
      padding: 10px;
      border-radius: 10px;
      margin-bottom: 30px;
      background-color: #ffffff;
      .chat-name {
        font-size: small;
        font-family: 'Helvetica';
        position: absolute;
        top: -20px;
        text-transform: capitalize;
      }
      .chat-timestamp {
        font-size: xx-small;
        color: rgba(35, 35, 35, 0.906);
        padding-inline: 5px;
      }

      &.sended-message {
        background-color: #dcf8c6;
        direction: rtl;
        margin-left: auto;
      }
    }
  }
  .chat-footer {
    display: flex;
    align-items: center;
    padding: 10px;
    &.hide-emoji {
      em-emoji-picker {
        display: none;
      }
    }
    em-emoji-picker {
      position: absolute;
      left:0;
      bottom: 30px;
    }
    form {
      display: flex;
      flex: 1;
      padding-inline: 5px;
      button {
        display: none;
      }
      input {
        background-color: #dddddd;
        border-radius: 20px;
      }
    }
  }
}
