.sign-in {
  display: grid;
  place-items: center;
  height: 100%;
  width: 100%;
  img {
    align-self: center;
    max-height: 400px;
  }
  form {
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    gap: 10px;
    max-width: 50vw;

    input {
      background-color: rgba(202, 202, 202, 0.556);
      color: black;
      font-size: 1.4rem;
      border-radius: 20px;
      &::placeholder {
        color: black;
      }
    }
  }
}
