.sidebar-container {
    display: flex;
    flex-direction: column;
    flex: 0.3;
    overflow: auto;
    .sidebar-header {
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: flex-start;
        height: fit-content;
        padding: 10px;

        .user-info {
            display: flex;
            align-items: center;
            gap: 15px;
            .user-name {
                font-size: 1.3rem;
            }
        }

        .sidebar-actions {
            border-right: 1px solid rgba(156, 156, 156, 0.075);
            display: flex;
            justify-content: flex-end;
            min-width: 8vw;
        }
    }

    .sidebar-search {
        padding: 10px;
    }

    .sidebar-chats {
        padding: 1rem;
        display: flex;
        flex-direction: column;
        user-select: none;
        cursor: pointer;

        .sidebar-chat-container {
            display: flex;
            align-items: center;
            padding: 10px;
            border-bottom: 1px solid rgba(215, 215, 215, 0.655);
            .sidebar-chat-info {
                margin-inline-start: 15px;
            }

            h2 {
                font-size: 1.1rem;
                margin-block: 0 8px;
                font-weight: 500;
            }

            p{
                font-size: 0.8rem;
                margin: 0;
            }
            &:hover {
                background-color: rgb(222, 222, 222);
            }
        }

    }
}